import React, { useState, useEffect } from 'react';
import "./Home.css"
import { Link } from 'react-router-dom';
import { 
  createGuestAccount, 
  createGuestBasket, 
  addShipping 
} from '../utils/Service'

const Home = () => {
  // const [edit, toggleEdit] = useState(false);

  useEffect(async () => {
    await createGuest();
    await createBasket();
  }, []);

  const createGuest = async () => {
    try {
      const response = await createGuestAccount();

      const token = response.token;
      const customerID = response.user.customerID;

       localStorage.setItem("ACCESS_TOKEN_KEY", token);
       localStorage.setItem("CUSTOMER_ID_KEY", customerID);

    } catch (error) {
      console.log('Create Guest Error:', error)
    }
  };

  const createBasket = async () => {
    try {
      const token = localStorage.getItem("ACCESS_TOKEN_KEY")
      const basket = await createGuestBasket(token);
      const { data } = basket;
      const { basket_id, product_sub_total, order_total, tax_total } = data;

      let shipping = {
        gift: false,
        shipment_id: 'me',
        shipping_method: {
          id: 'SNGInStore',
        },
        shipping_address: {
          first_name: 'John',
          last_name: 'Doe',
          city: 'Boston',
          country_code: 'US',
        },
      };

      let basketDetail= {
        items: [],
        subTotal: 0,
        total: 0,
        discount: 0,
        coupon: '',
        tax: 0,
        card: {},
        error: '',
        paymentInstruments: [],
        selectedCard: {},
        preferredAddress: '',
      }

      localStorage.setItem("BASKET_ID_KEY", basket_id);
      localStorage.setItem("BASKET_DETAIL", basketDetail);
      addShipping({ token, basket_id, shipping });
    } catch (error) {
      console.log('Create Basket Error:', error)
    }
  };

  // const handleEditPress = () => {
  //   const element = document.getElementById("add-button");
  //   if (element.style.display === "none") {
  //     element.style.display = "block";
  //   } else {
  //     element.style.display = "none";
  //   }

  //   toggleEdit(!edit);
  //   // localStorage.setItem("")
  // }

  return (
    <div id="app-container">
      <div id="button-container">
        <Link to="/edit" >
          <button id="add-button">
            Add
          </button>
        </Link>
      </div>
      {/* {edit ? <Edit handleEditPress={() => handleEditPress()} /> : null} */}
    </div>
  );
}

export default Home;
































