import React from 'react';
import { CarouselProvider, Slider, Slide, Image } from 'pure-react-carousel';
import { addProductToBasket } from '../utils/Service'
import 'bootstrap/dist/css/bootstrap.min.css';
import 'pure-react-carousel/dist/react-carousel.es.css';
import "./productCarousel.css"

const ProductCarousel = ({ balloons, setActiveBalloon, setActiveBalloonPrice, activeBalloonPrice}) => {

  const addBalloon = (balloon) => {
    window.addBalloon(`${balloon.represented_product.id}`);
    setActiveBalloonPrice(activeBalloonPrice += Number(balloon.price));
    addToBasket(balloon.represented_product.id);
  }

  const addToBasket = async (productID) => {
    let item = [{ "product_id": productID.toString(), "quantity": 1 }];

    try {
      const token = await localStorage.getItem("ACCESS_TOKEN_KEY");
      const basketID = await localStorage.getItem("BASKET_ID_KEY");
      
      const basket = await addProductToBasket({ token, basketID, item });
      const { data } = basket;
      const {
        product_items,
        product_sub_total,
        order_total,
        tax_total,
      } = data;

      console.log(data)

      const cart = {
        product_items,
        product_sub_total,
        order_total,
        tax_total,
      };

      console.log(cart)


      localStorage.setItem("CART", JSON.stringify(cart));
      } catch (error) {
      console.log('Add Item to Basket Error:', error);
    }
  }

  return (
      <div className="productCarousel">
      <CarouselProvider
        className="carousel-provider"
        isIntrinsicHeight={true}
        totalSlides={!!balloons && balloons.length}
        visibleSlides={2.2}
        currentSlide={0}
      >
        <Slider className="slider" classNameTray="tray" classNameTrayWrap="tray-wrap">
          {!!balloons && balloons.map((balloon, idx) => (
            <Slide index={idx} key={idx} className='product-slide'>
              <div 
                className="slide-container" 
                id={`active-product-${idx}`}
                onClick={() => setActiveBalloon(idx)}
              >
                <div className="image">
                  <img className="p-image" src={`https://partycity.scene7.com/is/image/PartyCity/_sq_?$_500x500_$&$product=PartyCity/${balloon.represented_product.id}`} />
                </div>
                <div className="name">
                  {balloon.product_name.toUpperCase()}
                </div>
                <div className="price">
                  ${balloon.price}
                </div>
                <div className="add" id={`add-${idx}`}>
                  <button className="button" onClick={() => addBalloon(balloon)}>
                    ADD
                  </button>
                </div>  
              </div>
            </Slide>
          ))}
        </Slider>
      </CarouselProvider>
    </div>
  );
}

export default ProductCarousel;

